.container {
    display: flex;
    justify-content: space-between;
    padding: 10px 15px 0px 10px;
    margin-bottom: 10px;
}

.banner {
    font-size: 25px;
    font-weight: bolder;
    font-family: 'Tilt Neon', cursive;
    display: flex;
    flex-direction: row-reverse;
}

.logo{
    margin-right: 5px;
}

.shopping-cart{
    display: flex;
    flex-direction: row;
}

.quantity{
    background-color: #EDB544;
    height: fit-content;
    border-radius: 50%;
    padding: 0 5px;
    position: absolute;
    right: 5px;
    top: 5px;
    font-weight: bold;
}
