.box-container-hide{
    background-color: #f5f5f2;
    width: 100%;
    height: fit-content;
    position: absolute;
    top: -100%;
    right: 0;
    border-top-right-radius:30px ;
    border-top-left-radius: 30px;
    transition: all 1500ms ease-in-out;
    z-index: 100;
}

.box-container{
    background-color: #f5f5f2;
    width: 100%;
    height: fit-content;
    position: absolute;
    top: 0;
    right: 0;
    border-bottom-right-radius:30px ;
    border-bottom-left-radius: 30px;
    transition: all 1500ms ease-in-out;
    z-index: 100;
}

.img-product img{
    width: 80%;
    height: auto;
    margin-top: 30px;
    margin-bottom: 10px;
    border: 5px solid #424242;
    border-radius: 30px;
}

.title-pro {
    font-family:'Tilt Neon', cursive;
    font-size: 30px;
    text-align: left;
    margin-left: 8%;
}

.price-pro {
    font-size: 25px;
    font-family: 'Tilt Neon', cursive;
    text-align: left;
    margin-left: 8%;
    color: #424242;
    margin-bottom: 2%;
}

.desc-pro {
    text-align: left;
    margin-left: 8%;
    margin-right: 5%;
    font-size: 15px;
    margin-bottom: 25px;
    color: #6b6b6b;
}

.btn-product {
    background-color: #F29E7D;
    width: 30%;
    padding: 7px 0px 7px 0px;
    border-radius: 10px;
    font-weight: bolder;
    font-family: 'Tilt Neon', cursive;
    font-size: 18px;
    margin: 20px auto;
    margin-top: 50px;
}

.colors ul{list-style:none; padding:0; margin: 0; align-items: start; display: flex; margin-left: 8%;}

.colors li{margin: 0 20px 0 0; display: inline-block;}

.colors input{display:none;}

.colors input[type="radio"]:checked + .swatch{box-shadow: inset 0 0 0 4px #fff;}

.swatch{
  display:inline-block;
  vertical-align: middle;
  height: 30px;
  width:30px;
  border: 1px solid #d4d4d4;
  border-radius: 50%;
}

.background-black {
    background-color: rgba(66, 66, 66, 0.582);
    width: 100%;
    height: 100%;
    z-index: 0;
    position: absolute;
    top: 0;
    opacity: 1;
    transition: all 2s ease;
}

.background-black-hide{
    background-color: rgba(66, 66, 66, 0.582);
    width: 100%;
    height: 100%;
    z-index: -200;
    position: absolute;
    opacity: 0;
    top: 0;
    transition: all 2s ease;
}

.btn-close {
    position: absolute;
    right: 0;
    top: -4;
    font-weight: bolder;
    padding:  10px 15px;
    border-radius: 50%;
}

